import { Injectable } from '@angular/core';
import { Users } from '../models/Users';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  constructor(private httpClient: HttpClient) { }


 // makes a post request to add a user
  addUser(user: Users): Observable<Users> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.httpClient.post<Users>('http://localhost:8080/user/adduser', user, httpOptions);
  }

  // gets the user from the DB by their id
  getUserByUserId(userId: number): Observable<Users> {
    return this.httpClient.get<Users>('http://localhost:8080/user/users/' + userId);
  }
}



